@import '../../styles/variaveis';

.principal {
  margin: 25px 30px;
  flex-grow: 1;
  flex-basis: auto;
  min-width: 400px;
  padding: 4em 2em 2em;
  box-sizing: border-box;
  display: flex;
  background: $fundo2;
  color: $branco;
  border-radius: 10px;
  gap: 2em;
}

.formulario {
  width: 100%;
  text-align: center;
  font-size: 20px;
}

.formulario label {
  display: block;
  font-weight: 700;
}

.formulario input {
  width: 100%;
  box-sizing: border-box;
  margin: 0.5em 0 1em;
  padding: 1em 0.5em;
  border-radius: 10px;
  border: 2px solid $fundo-botao;
  text-align: center;
}

.formulario input::placeholder {
  color: $texto-claro;
}